<template>
	<div
		class="recommendation"
		@mouseover="value.showHoverImage = true"
		@mouseout="value.showHoverImage = false"
	>
		<a href="https://www.google.com"></a>
		<div class="recommendation__layout recommendation__img">
			<img src="@/assets/images/article.png" v-if="!value.showHoverImage">
			<img src="@/assets/images/article_hov.png"  v-if="value.showHoverImage">
		</div>
		<div class="recommendation__layout recommendation__content">
			<h3 class="recommendation__title">サンプルテキストサンプルテキストサンプルテキストサンプルテキスト</h3>
			<div class="recommendation__date">2016.03.26</div>
		</div>
	</div> <!-- // recommendation -->
</template>

<script>
export default {
  name: 'recommendation',

	data() {
		return {}
	},

	props: {
		value: {}
	}
}
</script>



