<template>
	<div class="app-content">
		<div class="app-content__layout app-content__layout--articles">
			<div
				class="article"
				v-for="i in data"
				@mouseover="i.showHoverImage = true"
				@mouseout="i.showHoverImage = false"
			>
				<a href="https://www.google.com"></a>

				<div class="article__layout article__img">
					<img src="@/assets/images/article.png" v-if="!i.showHoverImage">
					<img src="@/assets/images/article_hov.png"  v-if="i.showHoverImage">
				</div>

				<div class="article__layout article__content">
					<div class="article__date">2016.01.01</div>
					<h3 class="article__title">サンプルテキストサンプルテキストサンプルテキストサンプルテキストサンプルテキスト</h3>
					<div class="article__desc">サンプルテキストサンプルテキストサンプルテキストサンプルテキストサンプルテキストサンプルテキストサンプルテキストサンプルテキス</div>
				</div>
			</div>  <!-- // article -->

			<div class="app-content__more">
				<a href="https://www.google.com"></a>
				<span>More</span>
				<i class="ico ico-arrow"></i>
			</div>
		</div>

		<div class="app-content__layout app-content__layout--sidebar">
			<sidebar></sidebar>
		</div>
	</div>  <!-- // app-content -->
</template>

<script>
import Sidebar from '@/components/common/sidebar/Sidebar'

export default {
  name: 'app-content',
	components: { Sidebar },

	data() {
		return {
			showHoverImage: false,

			data: [
				{ id: 1, showHoverImage: false },
				{ id: 2, showHoverImage: false },
				{ id: 3, showHoverImage: false },
				{ id: 4, showHoverImage: false },
				{ id: 5, showHoverImage: false },
				{ id: 6, showHoverImage: false },
				{ id: 7, showHoverImage: false },
				{ id: 8, showHoverImage: false },
				{ id: 9, showHoverImage: false },
				{ id: 10, showHoverImage: false }
			]
		}
	},

	method: {}
}
</script>



