<template>
	<div
		class="slider"
		:style="{ 'background-image': `url(${require(`../../assets/images/sliders/slider-${currentPage}.png`)})` }"
	>
		<div class="slider__content">
			<div class="slider__text">
				<div
					v-for="(i, key) in sliders"
					:class="`slider__text--${i}`"
					v-if="key === currentPage"
				>
					<h2>{{ sliders[key].title }}</h2>
					<small>{{ sliders[key].desc }}</small>
				</div>
			</div>

			<ul class="slider__arrow">
				<li class="slider__arrow--left" @click="navigatePage('prev')"></li>
				<li class="slider__arrow--right" @click="navigatePage('next')"></li>
			</ul>

			<ul class="slider__pager">
				<li
					v-for="(i, key) in sliders"
					@click="setActivePager(key)"
					:class="{ active: key === currentPage }"
				></li>
			</ul>

		</div>
	</div> <!-- // slider -->

</template>

<script>
export default {
  name: 'slider',

	data() {
		return {
			currentPage: 0,

			sliders: [
				{
					title: '1キャッチコピーが入ります。',
					desc: '1サンプルテキストサンプルテキストサンプルテキストサンプルテキスト サンプルテキストサンプルテキストサンプルテキストサンプルテキ'
				},
				{
					title: '2キャッチコピーが入ります。',
					desc: '2サンプルテキストサンプルテキストサンプルテキストサンプルテキスト サンプルテキストサンプルテキストサンプルテキストサンプルテキ'
				},
				{
					title: '3キャッチコピーが入ります。',
					desc: '3サンプルテキストサンプルテキストサンプルテキストサンプルテキスト サンプルテキストサンプルテキストサンプルテキストサンプルテキ'
				}
			]
		}
	},

	methods: {
		setActivePager(pageNo) {
			this.currentPage = pageNo
		},

		navigatePage(direction) {
			if (direction === 'next') {
				this.currentPage < this.sliders.length - 1 ? this.currentPage++ : ''
			} else {
				this.currentPage > 0 ? this.currentPage-- : this.currentPage * 1
			}
		}

	}
}
</script>



