<template>
	<div class="app-footer">
		<div class="app-footer__inner">
			<ul class="app-footer__layout app-footer__nav">
				<li>
					<a href="https://www.google.com"></a>
					<span>運営会社</span>
				</li>
				<li>
					<a href="https://www.google.com"></a>
					<span>個人情報保護について</span>
				</li>
			</ul>

			<div class="app-footer__layout app-footer__copyright">
				<span>©2016 Sample</span>
			</div>

			<div
				class="back-to-top"
				@click="backToTop()"
			></div>
			<!-- v-if="showBackToTopBtn" -->
		</div>
	</div>
</template>

<script>
export default {
  name: 'app-footer',

	created() {
		// this.
	},

	methods: {
		backToTop() {
			document.body.scrollTop = 0; // For Safari
			document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
		}
	},

	computed: {
		showBackToTopBtn() {
			return document.body.scrollTop > 20 || document.documentElement.scrollTop > 20 ? true : false
		}
	}
}
</script>



